import React, {useEffect} from 'react'
import CheckoutSteps from '../components/CheckoutSteps'
import { Link } from 'react-router-dom'
// for flex-box building 
import { Button, Row, Col, ListGroup, Image, Card } from 'react-bootstrap'
// product cart component
import {createOrder} from '../actions/orderActions'
// redux elems
import { useDispatch, useSelector } from 'react-redux'
import Message from '../components/Message'

const PlaceOrderScreen = ({history}) => {

    const cart = useSelector(state => state.cartList)
    const { shippingAddress, paymentMethod, cartItems } = cart
    const addDecimals = (num) => {
        return (Math.round((num * 100)/100)).toFixed(2)
    }

    cart.itemsPrice = cartItems.reduce( (accumulator, currentValue) => {
        return accumulator + +currentValue.price*currentValue.qty
    }, 0).toFixed(2)
    cart.shippingPrice = addDecimals(cart.itemsPrice > 100 ? 0 : 100)
    cart.taxPrice = Number((cart.itemsPrice * 0.15).toFixed(2))
    cart.totalPrice = (+cart.shippingPrice + +cart.itemsPrice + +cart.taxPrice).toFixed(2)

    const orderCreate = useSelector(state => state.orderCreate)
    const { order, success, error } = orderCreate

    useEffect( () => {
        if (success) {
            history.push(`/orders/${order._id}`)
        }
        // eslint-disable-next-line
    }, [history, success])

    const dispatch = useDispatch()
    const placeOrderHandler = (e) => {
        e.preventDefault()
        dispatch(createOrder({
            orderItems: cartItems,
            shippingAddress: shippingAddress,
            paymentMethod: paymentMethod,
            itemsPrice: cart.itemsPrice,
            shippingPrice: cart.shippingPrice,
            taxPrice: cart.taxPrice,
            totalPrice: cart.totalPrice
        }))
    }

    return (     
        <>
            <CheckoutSteps step1 step2 step3 step4/>
                 
            <Row>
                <Col md={8}>
                    <ListGroup variant="flush">
                         <ListGroup.Item>
                            <h2>
                                Shipping
                            </h2>
                            <p>
                                <strong> Address: </strong>
                                {shippingAddress.address},
                                {shippingAddress.city},
                                {shippingAddress.postalCode},
                                {shippingAddress.country}
                            </p>
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <h2>
                                Payment Method
                            </h2>
                            <strong> Method: </strong>
                            { paymentMethod }
                        </ListGroup.Item>

                        <ListGroup.Item>
                            <h2>
                                Order Items
                            </h2>
                            { 
                                cartItems.length === 0 ? <Message> Your cart is empty </Message>
                                :
                                (
                                    <ListGroup variant="flush">
                                        {
                                            cartItems.map( (item, index) => (
                                                    <ListGroup.Item key={index}>
                                                        <Row>
                                                            <Col md={1}>
                                                                <Image  src={item.image} 
                                                                        alt={item.name} 
                                                                        fluid 
                                                                        rounded
                                                                    />
                                                            </Col>
                                                            <Col>
                                                                <Link to={`/product/${item.product}`}>
                                                                    {item.name}
                                                                </Link>
                                                            </Col>
                                                            <Col md={4}>
                                                                    {+item.qty} x ${+item.price} = {+item.qty * +item.price}
                                                            </Col>
                                                            
                                                        </Row>
                                                    </ListGroup.Item>
                                            ))
                                        }
                                    </ListGroup>
                                )
                            }
                        </ListGroup.Item> 

                    </ListGroup>
                </Col>
                <Col md={4}>
                    <Card>
                       <ListGroup variant="flush">
                           <ListGroup.Item>
                               <h2> Order Summary </h2>
                           </ListGroup.Item>
                           <ListGroup.Item>
                               <Row>
                                   <Col>
                                        Items
                                   </Col>
                                   <Col>
                                   ${   
                                       cart.itemsPrice 
                                    }
                                   </Col>
                               </Row>
                           </ListGroup.Item>
                           <ListGroup.Item>
                               <Row>
                                   <Col>
                                        Shipping
                                   </Col>
                                   <Col>
                                   ${   
                                        cart.shippingPrice
                                    }
                                   </Col>
                               </Row>
                           </ListGroup.Item>
                           <ListGroup.Item>
                               <Row>
                                   <Col>
                                        Tax Price
                                   </Col>
                                   <Col>
                                   ${   
                                        cart.taxPrice
                                    }
                                   </Col>
                               </Row>
                           </ListGroup.Item>
                           <ListGroup.Item>
                               <Row>
                                   <Col>
                                        Total
                                   </Col>
                                   <Col>
                                   ${   
                                        cart.totalPrice
                                    }
                                   </Col>
                               </Row>
                           </ListGroup.Item>
                           
                                    <ListGroup.Item>
                                        {error && <Message variant="danger"> {error} </Message>}
                                    </ListGroup.Item>
                         
                           <ListGroup.Item>
                              <Button 
                                    type="button" 
                                    className="btn-block" 
                                    disabled={cart.cartItems.length === 0}
                                    onClick={(e) => placeOrderHandler(e)}
                                >  
                                Place Order
                              </Button>
                           </ListGroup.Item>
                       </ListGroup> 
                    </Card>
                </Col>

            </Row>
        </>
    )
}

export default PlaceOrderScreen
