import React, {useEffect} from 'react'
import { LinkContainer } from 'react-router-bootstrap'
// for flex-box building 
import { Table, Button } from 'react-bootstrap'
// redux elems
import { useDispatch, useSelector } from 'react-redux'
// UI components
import Loader from '../components/Loader'
import Message from '../components/Message'
// action to get all users
import { listUsers } from '../actions/userAction'
import { deleteUser } from '../actions/userAction'

const UserListScreen = ({ history }) => {

    const dispatch = useDispatch()

    const usersList = useSelector(state => state.userList)
    const { loading, error, users } = usersList

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    const userDelete = useSelector(state => state.deleteUser)
    const { success:successDelete } = userDelete

    useEffect( () => {
        if (userInfo && userInfo.isAdmin) {
            dispatch(listUsers())
        } else {
            history.push('/login')
        }
    }, [dispatch, history, successDelete, userInfo])
    
    const deleteHandler = (id) => {
        if(window.confirm('Are you sure')){
            dispatch(deleteUser(id))
        }
    }

    return (
        <>
            <h1> Users </h1>
            {
                loading ? <Loader/> : error ? <Message variant="danger"> {error} </Message>
                
                : (
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                <th> ID </th>
                                <th> NAME </th>
                                <th> EMAIL </th>
                                <th> ADMIN </th>
                                <th>  </th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                users.map( (user) => (
                                    <tr key={user._id}>
                                        <td> {user._id} </td>
                                        <td> {user.name} </td>
                                        <td> <a href={`mailto:${user.emil}`}>{user.email}</a> </td>
                                        <td> {
                                                user.isAdmin ? 
                                                <i style={{color: 'green'}} className="fas fa-check"></i> 
                                                : <i style={{color: 'red'}} className="fas fa-check"></i>
                                            } 
                                        </td>
                                        <td>
                                            <LinkContainer to={`/admin/users/${user._id}/edit`}>
                                                <Button variant="light" className="btn-sm">
                                                    <i className="fas fa-edit">
                                                    </i>
                                                </Button>
                                            </LinkContainer>
                                            <Button 
                                                variant="danger" 
                                                className="btn-sm" 
                                                onClick={() => deleteHandler(user._id)}>
                                                    <i className="fas fa-trash">
                                                    </i>
                                            </Button>    
                                        </td>
                                     </tr>
                                 ) )
                            }
                        </tbody>
                    </Table>
                )
            }
           
        </>
    )
}

export default UserListScreen
